import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewClickStop.module.scss';
import {
  IDevice,
  DeviceOrientation,
  DeviceType
 } from '../mobilePreview/MobilePreview';

export interface IMobilePreviewClickStopProps {
  device: IDevice;
  onClick: (device: IDevice) => void;
  selected: boolean;
  orientation: DeviceOrientation;
  deviceType: DeviceType;
}

export default class MobilePreviewClickStop extends React.Component<IMobilePreviewClickStopProps, void> {
  constructor(props: IMobilePreviewClickStopProps) {
    super(props);
  }

  public render(): React.ReactElement<IMobilePreviewClickStopProps> {
    let effectiveWidth: number = this.props.device.width;

    if (this.props.deviceType === DeviceType.Phone) {
      if (this.props.orientation === DeviceOrientation.Landscape) {
        effectiveWidth = this.props.device.height;
      }
    } else if (this.props.deviceType === DeviceType.Tablet) {
      if (this.props.orientation === DeviceOrientation.Portrait) {
        effectiveWidth = this.props.device.height;
      }
    }

    const leftCalc: string = `calc(50% - ${effectiveWidth / 2}px)`;
    const divStyle: React.CSSProperties = {
        width: effectiveWidth,
        left: leftCalc
    };

    const className: string = css(styles.navBarItem, 'ms-borderColor-white', styles.clickStop,
      {
        [styles.clickStopSelected]: this.props.selected
      }
    );

    return (
      <div className={ className }
        onClick={ () => this.props.onClick(this.props.device) } style={ divStyle }>
      </div>
    );
  }
}